<template>
  <div class="basket">
    <div class="basket__inner">
      <AppDrawerHead />

      <div v-if="!totalPrice || orderId" class="basket-wrapper">
        <InfoBlock
          v-if="!totalPrice && !orderId"
          title="Корзина пустая"
          description="Добавьте хотя бы одну пару кроссовок, чтобы сделать заказ."
          :img-url="require('@/img/package-icon.png')"
        />
        <InfoBlock
          v-if="orderId"
          title="Заказ оформлен!"
          :description="`Ваш заказ #${orderId} скоро будет передан курьерской доставке`"
          :img-url="require('@/img/order-success-icon.png')"
        />
      </div>

      <div v-else class="basket-content">
        <AppCartItemList />

        <div class="basket__order">
          <div class="basket__order-item">
            <p class="basket__order-text">Итого:</p>
            <span></span>
            <p class="basket__order-price">{{ totalPrice }} руб.</p>
          </div>
          <div class="basket__order-item">
            <p class="basket__order-text">Налог 5%:</p>
            <span></span>
            <p class="basket__order-price">{{ vatPrice }} руб.</p>
          </div>
          <app-button
            class="basket__order-btn"
            @click="createOrder"
            :disabled="buttonDisabled"
          >
            <span>Оформить заказ</span>
            <img src="@/img/arrow-next.svg" alt="arrow" />
          </app-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { inject, computed } from "vue";
import AppDrawerHead from "./AppDrawerHead.vue";
import AppCartItemList from "./AppCartItemList.vue";
import InfoBlock from "./InfoBlock.vue";

export default {
  data() {
    return {
      orderId: null,
      isCreatingOrder: false,
      cartIsEmpty: computed(() => this.cart.length === 0),
      buttonDisabled: computed(() => this.isCreatingOrder || this.cartIsEmpty),
    };
  },
  components: {
    AppDrawerHead,
    AppCartItemList,
    InfoBlock,
  },
  props: {
    totalPrice: Number,
    vatPrice: Number,
  },
  methods: {
    async createOrder() {
      try {
        this.isCreatingOrder = true;
        const { data } = await axios.post(`https://f49d335ceaacc67f.mokky.dev/orders`, {
          items: this.cart,
          totalPrice: this.totalPrice,
        });

        this.cart = [];
        this.orderId = data.id;
      } catch (err) {
        console.log(err);
      } finally {
        this.isCreatingOrder = false;
      }
    },
  },
  setup() {
    const { cart, closeDrawer } = inject('cart');

    return {
      cart,
      closeDrawer
    }
  }
};
</script>

<style lang="scss" scoped>
.basket {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.5);
  z-index: 100;

  &-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &-content{
    height: calc(100% - 60px);
    flex-grow: 1;
    display: flex;
    flex-direction: column; 
  }

  &__inner {
    background: #fff;
    box-shadow: -10px 4px 24px 0px rgba(0, 0, 0, 0.1);
    max-width: 385px;
    margin-left: auto;
    height: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
  }

  &__order {
    display: flex;
    flex-direction: column;
    gap: 19px;
  }

  &__order-item {
    display: flex;
    gap: 8px;
    span {
      flex-grow: 1;
      border-bottom: 1px dashed #dfdfdf;
      margin-bottom: 4px;
    }
  }

  &__order-text {
    font-size: 16px;
  }

  &__order-price {
    font-weight: 600;
    font-size: 16px;
  }

  &__order-btn {
    margin-top: 5px;
    img {
      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>
