<template>
  <AppDrawer
    v-if="drawerOpen"
    :total-price="totalPrice"
    :vat-price="vatPrice"
  />
  <div class="app bg-white w-4/5 m-auto rounded-xl shadow-xl my-10 leading-12">
    <AppHeader :total-price="totalPrice" @open-drawer="openDrawer" />

    <div class="catalog"> 
      <div class="container">
        <div class="catalog__inner">
          <router-view></router-view>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { provide, ref, computed, watch } from "vue";

import AppHeader from "@/components/AppHeader.vue";
import AppDrawer from "@/components/AppDrawer.vue";

export default {
  name: "App",
  components: {
    AppHeader,
    AppDrawer,
  },
  data() {
    return {
      totalPrice: computed(() => this.cart.reduce((acc, item) => acc + item.price, 0)),
      vatPrice: computed(() => Math.round((this.totalPrice * 5) / 100)),
    };
  },
  methods: {

  },
  watch: {
    cart: {
      handler(newCart) {
        this.items = this.items.map((item) => ({
          ...item,
          isAdded: false,
        }));
      },
    },
  },
  setup() {
    const drawerOpen = ref(false);
    const cart = ref([]);
    const items = ref([]);

    watch(
      cart,
      () => {
        localStorage.setItem("cart", JSON.stringify(cart.value));
      },
      { deep: true }
    );

    const closeDrawer = () => {
      drawerOpen.value = false;
    };
    const openDrawer = () => {
      drawerOpen.value = true;
    };
    const addToCart = (item) => {
      cart.value.push(item);
      item.isAdded = true;
    };
    const removeFromCart = (item) => {
      let index = -1;
      for (let i = 0; i < cart.value.length; i++) {
        if (cart.value[i].id === item.id) {
          index = i;
          break;
        }
      }
      if (index !== -1) {
        cart.value.splice(index, 1);
      }
      items.value.forEach((pl) => {
        if (pl.id === item.id) {
          pl.isAdded = false;
        }
      });
    };

    provide("cart", {
      cart,
      items,
      closeDrawer,
      openDrawer,
      addToCart,
      removeFromCart,
    });

    return {
      closeDrawer,
      openDrawer,
      drawerOpen,
      cart,
      items,
      addToCart,
      removeFromCart,
    };
  },
};
</script>

<style lang="scss">
.catalog {
  padding: 40px 0;
}
</style>
