<template>
  <div>
    <div class="catalog__box">
      <button></button>
      <h1 class="catalog__title">Мои закладки</h1>
    </div>

    <AppCardList
      :items="favorites"
      is-null
      @add-to-favorite="(item) => check(item)"
    />
  </div>
</template>

<script>
  import axios from 'axios';
  import AppCardList from "../components/AppCardList.vue";
  import { mapActions } from 'vuex';

  export default {
    components: {
      AppCardList
    },
    data() {
      return {
        favorites: []
      }
    },
    methods: {
      ...mapActions({
        addToFavorite: 'favorite/addToFavorite'
      }),
      check(item) {
        this.addToFavorite(item);
        this.favorites.splice(this.favorites.indexOf(item), 1);
      }
    },
    async mounted() {
      try{
        const {data} = await axios.get(`https://f49d335ceaacc67f.mokky.dev/favorites`);
        this.favorites = data.map(obj => obj.item);
        this.favorites.map((obj, index) => obj.favoriteId = data[index].id)
      } catch(err) {
        console.log(err);
      }
    },
    setup() {
    }
  }
</script>

<style lang="scss" scoped>
.catalog{
  &__box {
    display: flex;
    margin-bottom: 40px;
  }
  &__title {
    font-size: 32px;
    font-weight: 700;
  }
}
</style>