<template>
  <div>
    <div class="catalog__box">
      <h1 class="catalog__title">Все кроссовки</h1>

      <div class="catalog__info">
        <app-select v-model="filters.selectSort" :options="sortOptions" />
        <app-input v-model="filters.searchQuery" placeholder="Поиск..." />
      </div>
    </div>

    <AppCardList
      :items="items"
      @add-to-cart="(item) => onClickAddPlus(item)"
      @add-to-favorite="(item) => this.addToFavorite(item)"
    />
  </div>
</template>

<script>
import axios from "axios";
import { inject, ref } from "vue";
import AppCardList from "../components/AppCardList.vue";
import { mapActions } from "vuex";

export default {
  name: "Home",
  components: {
    AppCardList,
  },
  data() {
    return {
      filters: {
        searchQuery: "", // инпут
        selectSort: "title", // выбранный селект
      },
      sortOptions: [
        { value: "title", name: "По названию" },
        { value: "price", name: "По цене (сначала дешвые)" },
        { value: "-price", name: "По цене (сначала дорогие)" },
      ],
    };
  },
  methods: {
    ...mapActions({
      addToFavorite: "favorite/addToFavorite",
    }),
    async fetchFavorites() {
      try {
        const { data: favorites } = await axios.get(
          `https://f49d335ceaacc67f.mokky.dev/favorites`
        );

        this.items = this.items.map((item) => {
          const favorite = favorites.find((favorite) => favorite.item_id === item.id);

          if (!favorite) {
            return item;
          }

          return {
            ...item,
            isFavorite: true,
            favoriteId: favorite.id,
          };
        });
      } catch (err) {
        console.log(err);
      }
    },
    async fetchItems() {
      try {
        const params = {
          sortBy: this.filters.selectSort,
        };

        if (this.filters.searchQuery) {
          params.title = `*${this.filters.searchQuery}*`;
        }

        const { data } = await axios.get(`https://f49d335ceaacc67f.mokky.dev/items`, {
          params,
        });
        this.items = data.map((obj) => ({
          ...obj,
          isFavorite: false,
          favoriteId: null,
          isAdded: false,
        }));
      } catch (err) {
        console.log(err);
      }
    },
    load() {
      this.fetchItems().then(() => {
        this.items = this.items.map((item) => ({
          ...item,
          isAdded: this.cart.some((cartItem) => cartItem.id === item.id),
        }));
        this.fetchFavorites();
      });
    },
  },
  mounted() {
    const localCart = localStorage.getItem("cart");
    this.cart = localCart ? JSON.parse(localCart) : [];

    this.load();
  },
  watch: {
    "filters.selectSort"() {
      this.load();
    },
    "filters.searchQuery"() {
      this.load();
    },
  },
  setup() {
    const { cart, addToCart, removeFromCart, items } = inject("cart");

    const onClickAddPlus = (item) => {
      if (!item.isAdded) {
        addToCart(item);
      } else {
        removeFromCart(item);
      }
    };

    return {
      onClickAddPlus,
      items,
      cart,
    };
  },
};
</script>

<style lang="scss" scoped>
.catalog {
  &__box {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
  }

  &__info {
    display: flex;
    gap: 30px;
  }
}
</style>
