import axios from "axios";

export const favoriteModule = {
  actions: {
    async addToFavorite(_, item) {
      try{
        if(!item.isFavorite) {
          const obj = {
            item_id: item.id,
            item: item
          }

          item.isFavorite = true;
          const { data } = await axios.post(`https://f49d335ceaacc67f.mokky.dev/favorites`, obj);
          item.favoriteId = data.id;
        } else {
          item.isFavorite = false;
          await axios.delete(`https://f49d335ceaacc67f.mokky.dev/favorites/${item.favoriteId}`);
          item.favoriteId = null;
        }
      } catch(err) {
        console.log(err);
      }
    }
  },
  namespaced: true
}