<template>
  <div class="basket__list" v-auto-animate>
    <AppCartItem
      v-for="item in cart"
      :key="item.id"
      :title="item.title"
      :filename="item.filename"
      :price="item.price"
      @on-click-remove="() => removeFromCart(item)"
    />
  </div>
</template>

<script>
import AppCartItem from "./AppCartItem.vue";
import { inject } from "vue";

export default {
  components: {
    AppCartItem,
  },
  setup() {
    const { cart, removeFromCart } = inject("cart");
    return { cart, removeFromCart };
  },
};
</script>

<style lang="scss" scoped>
.basket__list {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  overflow-y: auto;
  margin-bottom: 10px;

  scrollbar-color: #979797 transparent;
  scrollbar-width: auto;

  &::-webkit-scrollbar {
    width: 5px;
    color: #000;
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #d3d2d2;
    border-radius: 5px;
    cursor: pointer;
  }
}
</style>
